import React, { useEffect, useRef, useState } from 'react';
import { WidgetWrapper } from './style';
import moment from 'moment';
import * as d3 from 'd3';

const D3 = props => {
  const ref = useRef(null);

  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    function handleResize() {
      setWidth(window.innerWidth);
    }
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [width]);

  useEffect(() => {
    const svg = d3.select(ref.current);

    const margin = 80;
    const width = ref.current.parentNode.clientWidth - margin - margin;

    const height = props.height - 2 * margin;

    const chart = svg
      .append('g')
      .attr('transform', `translate(${margin}, ${margin})`);

    //scale
    const xScale = d3
      .scaleBand()
      .range([0, width])
      .domain(props.data.map(s => moment(s.month).format('L')))
      .padding(0.4);

    var x1Array = Object.keys(props.data[0]);
    x1Array.shift();

    const x1 = d3
      .scaleBand()
      .range([0, xScale.bandwidth()])
      .domain(x1Array);

    const yScale = d3
      .scaleLinear()
      .range([height, 0])
      .domain([0, 500]);

    var colors = d3.scaleOrdinal(['#d6e9c6', '#bce8f1', '#faebcc', '#ebccd1']);
    const makeYLines = () => d3.axisLeft().scale(yScale);

    //Chart
    chart
      .append('g')
      .attr('transform', `translate(0, ${height})`)
      .call(d3.axisBottom(xScale));

    chart.append('g').call(d3.axisLeft(yScale));

    chart
      .append('g')
      .attr('class', 'grid')
      .call(
        makeYLines()
          .tickSize(-width, 0, 0)
          .tickFormat('')
      );
    //https://zhuanlan.zhihu.com/p/38032622
    //https://observablehq.com/@d3/grouped-bar-chart
    //BAR
    const groupKey = 'month';
    const keys = ['ApprovedCount', 'DeclinedCount'];
    var dynamicColor;
    var group = chart
      .append('g')
      .selectAll('g')
      .data(props.data)
      .join('g')
      .attr('transform', d => {
        return `translate(${xScale(moment(d[groupKey]).format('L'))},0)`;
      })
      .selectAll('rect')
      .data(d => {
        return keys.map(key => ({ key, value: d[key] }));
      })
      .enter();
    group
      .append('rect')
      .attr('x', d => {
        return x1(d.key);
      })
      .attr('y', d => yScale(d.value))
      .attr('width', x1.bandwidth())
      .attr('height', d => yScale(0) - yScale(d.value))
      .attr('fill', d => colors(d.key))
      .attr('stroke', '#31708f')
      .attr('stroke-width', '1')
      .on('mouseenter', function(actual, i) {
        dynamicColor = this.style.fill;
        d3.select(this)
          .transition()
          .duration(300)
          .style('fill', '#3c763d');
      })
      .on('mouseleave', function() {
        d3.selectAll('.value').attr('opacity', 1);

        d3.select(this)
          .transition()
          .duration(300)
          .attr('opacity', 1)
          .style('fill', dynamicColor);
      });

    group
      .append('text')
      .attr('class', 'value')
      .attr('x', d => {
        return x1(d.key) + x1.bandwidth() / 2;
      })
      .attr('y', d => yScale(d.value))
      .attr('height', 5)
      .attr('text-anchor', 'middle')
      .text(a => `${a.value}`);

    // barGroups
    // .append('text')
    // .attr('class', 'value')
    // .attr('x', (a) =>{
    //     return (xScale(moment(a.month).format('L')) + xScale.bandwidth()/2)
    // })
    // .attr('y', (a) => yScale(a.ApprovedCount) )
    // .attr('text-anchor', 'middle')
    // .text((a) => `${a.ApprovedCount}`)

    svg
      .append('text')
      .attr('class', 'label')
      .attr('x', -(height / 2) - margin)
      .attr('y', margin / 2.4)
      .attr('transform', 'rotate(-90)')
      .attr('text-anchor', 'middle')
      .text('Count');

    svg
      .append('text')
      .attr('class', 'label')
      .attr('x', width / 2 + margin)
      .attr('y', height + margin * 1.7)
      .attr('text-anchor', 'middle')
      .text('Month');

    svg
      .append('text')
      .attr('class', 'title')
      .attr('x', width / 2 + margin)
      .attr('y', 40)
      .attr('text-anchor', 'middle')
      .text('Claim Count');

    var legend = svg
      .append('g')
      .attr('class', 'legend')
      .attr('transform', 'translate(' + width + ', 0)');
    legend
      .selectAll('rect')
      .data(keys)
      .enter()
      .append('rect')
      .attr('x', 0)
      .attr('y', function(d, i) {
        return i * 18;
      })
      .attr('width', 12)
      .attr('height', 12)
      .attr('fill', function(d, i) {
        return colors(d);
      });

    legend
      .selectAll('text')
      .data(keys)
      .enter()
      .append('text')
      .text(function(d) {
        return d;
      })
      .attr('x', 18)
      .attr('y', function(d, i) {
        return i * 18;
      })
      .attr('text-anchor', 'start')
      .attr('alignment-baseline', 'hanging');

    return () => {
      svg.selectAll('*').remove();
    };
  }, [props.data, props.height, width]);

  return (
    <WidgetWrapper className="isoSaleWidget">
      <svg width={props.width} height={props.height} ref={ref}></svg>
    </WidgetWrapper>
  );
};

export default D3;
